// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import Vuex from 'vuex'
import VueWeui from 'vue-weui'
import weui from 'weui'
import VueResource from 'vue-resource'
import AMap from 'vue-amap'
import wx from 'weixin-js-sdk'

Vue.use(Vuex)
Vue.use(VueWeui)
Vue.use(weui)
Vue.use(VueResource)
Vue.use(AMap)
Vue.use(wx)
// vue-amap
AMap.initAMapApiLoader({
  // key
  key: '096a5df2b8da44c46731d84af3ca9df3',
  // 
  plugin: ['CitySearch']
})
import './assets/css/global.scss'
import './assets/css/style.css'

Vue.config.productionTip = false

const store = new Vuex.Store({
  // 
  state: {
    city: sessionStorage.getItem('city') ? sessionStorage.getItem('city') : '',
    model: sessionStorage.getItem('model') ? sessionStorage.getItem('model') : '',   // 
    cityActive: true,  // 
    vipPrice: 0, // 
    modelSelected: sessionStorage.getItem('modelId') ? sessionStorage.getItem('modelId') : 0   //  id
  },
  // ,
  // mutationsVuex
  mutations: {},
  // storegettersstoreGettersstate
  getters: {},
  actions: {}
})
// mutations

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  template: '<App/>',
  components: { App },
  store
})
function randomChar (l) {
  let x = '0123456789qwertyuioplkjhgfdsazxcvbnm'
  let tmp = ''
  for (let i = 0; i < l; i++) {
    tmp += x.charAt(Math.ceil(Math.random() * 100000000) % x.length)
  }
  return tmp
}
let nonceStr = randomChar(8)
let myTimestamp = Date.parse(new Date()).toString().substr(0, 10)
// Vue.prototype.url = 'http://' + window.location.host
Vue.prototype.url = 'http://www.kuangjifix.com'
Vue.prototype.wxShare = function () {
// 
  this.$http.get('list/wxapi/sign', {
    url: window.location.href.split('#')[0],
    noncestr: nonceStr,
    timestamp: myTimestamp
  }).then(res => {
    // 
    console.log(res)
    // configreadyconfigconfig
    wx.config({
      debug: false, // ,apialertpclogpc
      appId: 'wx98dfa82a63d14ac9',   // 
      timestamp: myTimestamp, // 
      nonceStr: nonceStr,   // 
      signature: res.signature, // 1
      jsApiList: ['onMenuShareAppMessage', 'onMenuShareTimeline'] // JSJS2
    })
  })
  wx.ready(() => {
    // ready
    // ready
    wx.onMenuShareAppMessage({ // 
      title: '', // 
      desc: ' - ',   // 
      link: 'http://wx.kuangjifix.com/svip_index',   //  
      imgUrl: 'https://wx.kuangjifix.com/img/round-logo.png', // 
      // 
      success: function () {
        // console.log('')
      },
      // 
      cancel: function () {
        console.log('')
      }
    })
    // 
    wx.onMenuShareTimeline({
      title: '', // 
      desc: ' - ',
      link: 'http://wx.kuangjifix.com/svip_index',
      imgUrl: 'https://wx.kuangjifix.com/img/round-logo.png',
      // 
      success: function () {
        // console.log('')
      },
      // 
      cancel: function () {
        console.log('')
      }
    })
  })

  wx.error(function (res) {
    // configerrorconfigdebug
    console.log(res)
  })
}




